___CSS_LOADER_EXPORT___.push([module.id, `* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Arial, Helvetica, sans-serif;
  background-color: #fdfaf6;
  color: #333;
  line-height: 1.6;
  display: flex;
  flex-direction: column;
  height: 100vh;
}

header {
  background-color: #b22222; /* deep red */
  padding: 1rem 0;
  text-align: center;
  width: 100%;
}

nav button {
  background-color: transparent;
  border: 2px solid #ffffff;
  color: #ffffff;
  padding: 0.6rem 1.2rem;
  margin: 0 0.5rem;
  cursor: pointer;
  font-size: 1.5rem;
  border-radius: 25px;
  font-family: "Italianno", cursive;
  font-weight: 500;
  transition: background-color 0.3s, color 0.3s,;
}

nav button:hover {
  background-color: #ffffff;
  color: #b22222;
}

#content {
  max-width: 700px;
  margin: 2rem auto;
  padding: 0 1rem;
  flex: 1;
}

#content h1 {
  text-align: center;
  color: #006400;
  font-family: "Italianno", cursive;
  font-size: 3rem;
}

section {
  border-bottom: 1px solid #ccc;
  margin-bottom: 20px;
  padding-bottom: 20px;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
  margin: 1rem 0;
  border-radius: 10px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

section img {
  max-width: 500px;
}

section h2 {
  color: #8b0000;
  font-family: "Italianno", cursive;
  font-size: 2.2rem;
}

strong {
  color: #b22222;
  font-weight: bold;
}

footer {
  text-align: center;
  padding: 1rem;
  background-color: #b22222;
  color: white;
  font-size: 0.9rem;
}

footer a {
  color: #ffffff;
  text-decoration: none;
  display: inline-flex;
  align-items: center;
  font-weight: bold;
}

footer a:hover {
  text-decoration: underline;
}
